'use strict';

// MODULES //

import stride2offset from '@stdlib/strided-base-stride2offset';
import ndarray from './ndarray.js';


// MAIN //

/**
* Adds a scalar constant to each single-precision floating-point strided array element and computes the sum using pairwise summation.
*
* ## Method
*
* -   This implementation uses pairwise summation, which accrues rounding error `O(log2 N)` instead of `O(N)`. The recursion depth is also `O(log2 N)`.
*
* ## References
*
* -   Higham, Nicholas J. 1993. "The Accuracy of Floating Point Summation." _SIAM Journal on Scientific Computing_ 14 (4): 783–99. doi:[10.1137/0914050](https://doi.org/10.1137/0914050).
*
* @param {PositiveInteger} N - number of indexed elements
* @param {number} alpha - scalar constant
* @param {Float32Array} x - input array
* @param {integer} strideX - stride length
* @returns {number} sum
*
* @example
* import Float32Array from '@stdlib/array-float32';
*
* var x = new Float32Array( [ 1.0, -2.0, 2.0 ] );
*
* var v = sapxsumpw( x.length, 5.0, x, 1 );
* // returns 16.0
*/
function sapxsumpw( N, alpha, x, strideX ) {
	return ndarray( N, alpha, x, strideX, stride2offset( N, strideX ) );
}


// EXPORTS //

export default sapxsumpw;
